<template>
    <div class="total">
        <div class="left">
            <img src="../../../assets/section1/lun3.jpeg" alt="图片">
            <div class="font" @mouseenter="changeBack">
                <p>缘定永恒礼包</p>
                <span>2023耕耘礼包</span>
                <div class="bottom">
                    <a href="">
                        查看
                        <i>icon</i>
                    </a>
                </div>
            </div>
        </div>
        <!-- 轮播图 -->
        <!--主体为swiper标签-->
        <!--属性 :options 绑定的是data中设置的swiper配置项-->
        <!--属性 ref 用于获取该dom元素，在计算属性computed中将被调用-->
        <!--属性 id 为swiper最外层容器设置css样式-->
        <swiper :options="swiperOption" ref="mySwiper" id="mySwiper">

            <!--必须的组件-->
            <!--每页幻灯片使用swiper-slide标签-->
            <!--幻灯片组件生成的标签自带.swiper-slide的类名，但单类名选择器设置的部分css(如宽高)将被覆盖-->
            <swiper-slide class="swiper_slide_item" v-for="(item, index) in swiperImgList" :key="item.id">
                <img :src="item.src" alt="">
                <!-- <div style="">水印</div> -->
                <!-- <CompA /> -->
            </swiper-slide>

            <!-- 可选的控件 -->
            <!--分页器-->
            <div class="swiper-pagination" slot="pagination"></div>
            <!--滚动条-->
            <!-- <div class="swiper-scrollbar" slot="scrollbar"></div> -->
            <!--前进后退按钮-->
            <!-- <div class="swiper-button-prev" slot="button-prev"></div>
            <div class="swiper-button-next" slot="button-next"></div> -->
        </swiper>

        <!--配置自定义的页面跳转按钮，to(page)为自定义方法，其内调用了swiper的内置方法-->
        <!-- <button @click="to(1)">点击跳转到Slide 1</button>
        <button @click="to(2)">点击跳转到Slide 2</button>
        <button @click="to(3)">点击跳转到Slide 3</button> -->


    </div>
</template>

<script>

const dynaimicRequire = (path) => require(`@/assets/section1/${path}.jpeg`)
export default {
    data() {
        return {
            swiperImgList: [
                { id: 1, src: dynaimicRequire('lun1') },
                { id: 2, src: dynaimicRequire('lun2') },
                { id: 3, src: dynaimicRequire('lun3') },
                { id: 4, src: dynaimicRequire('lun4') },
                { id: 5, src: dynaimicRequire('lun5') },
                { id: 6, src: dynaimicRequire('lun6') },
                { id: 7, src: dynaimicRequire('lun7') },
                { id: 8, src: dynaimicRequire('lun8') },
                { id: 9, src: dynaimicRequire('lun9') },
                { id: 10, src: dynaimicRequire('lun10') }
            ],
            //swiperOption：swiper配置项信息，需要绑定在swiper标签的 :option 属性中
            swiperOption: {

                // 幻灯片放映方向
                // direction: 'vertical', //通常不与左右按钮一同使用

                //分页器配置项
                pagination: {
                    el: ".swiper-pagination", //分页器的类名
                    clickable: true, // 点击分页器跳切换到相应的幻灯片
                    type: 'progressbar', //小圆点|进度条|数字页码
                    dynamicBullets: true,  //动态小圆点(type:'bullets'时)
                    //自定义分页器，需设置样式
                    renderBullet(index, className) {
                        return `<span class="${className} swiper-pagination-bullet-custom">${index + 1}</span>`
                    },
                },
                //滚动条
                // scrollbar: {
                //     el: '.swiper-scrollbar',
                //     hide: true
                // },template

                //幻灯片播放配置项
                loop: true, //是否循环播放
                speed: 1000, // 发生页面切换动画时，动画的切换速度
                autoplay: {
                    delay: 2000, // 幻灯片停留时间
                    disableOnInteraction: false, // 用户操作swiper之后，是否禁止autoplay
                    stopOnLastSlide: true, // 切换到最后一个slide时是否停止自动切换。（loop模式下无效）。
                },
                on: {
                    slideChangeTransitionEnd: function () {
                        // console.log(this.activeIndex); //每次切换结束时，在控制台打印现在是第几个slide
                    },
                },
            },
        };
    },

    //计算属性
    computed: {
        swiper() {
            return this.$refs.mySwiper.$swiper;
        },
    },

    //设置自定义的按钮，点击后将前往目标也页
    methods: {
        to(index) {
            this.swiper.slideTo(index)
            console.log(this.swiper);
        },
        changeBack() {

        }

    }
}
</script>

<style lang="less" scoped>
.total {
    width: 686px;
    height: 431px;
    flex: 0 0 auto;
    display: flex;

    // 左边盒子
    .left {
        width: 50%;
        position: relative;

        img {
            width: 100%;
            height: 100%;
        }

        .font {
            width: 100%;
            height: 100%;
            position: absolute;
            background-color: rgba(0, 0, 0, 0.5);
            left: 0;
            top: 0;
            text-align: center;
            color: #fff;

            &:hover {
                cursor: pointer;
            }

            a {
                display: inline-block;
                width: 150px;
                height: 30px;
                line-height: 30px;
                font-style: normal;
            }

            p {
                margin-top: 190px;
                font-size: 22px;
                font-weight: bold;
            }

            .bottom {
                width: 140px;
                height: 36px;
                margin: 40px auto 0;

                a {
                    color: red;
                }
            }

            &:hover a {
                background: red;
                color: white;
            }

        }
    }

    .swiper-pagination {
        position: absolute;
        bottom: 10px;
        left: 50%;
        transform: translateX(-50%);
    }

    .swiper-container {
        width: 50%;
    }
}
</style>
