<template>
    <div class="header-banner-total">
        <div :class="dynamicClass"> </div>
        <div class="header-banner">
            <ul class="banner-ul" @mouseover="increase" @mouseleave="remove">
                <li><a href="">
                        <span>宇宙战</span>
                        <span>DNFU</span>
                    </a>
                </li>
                <li><a href="">
                        <span>游戏资料</span>
                        <span>GAME INFO</span></a></li>
                <li><a href="">
                        <span>活动</span>
                        <span>ACTIVITY</span>
                    </a></li>
                <li><a href="">
                        <span>赛事</span>
                        <span>EVENT</span>
                    </a></li>
                <li>
                    <a href="">
                        <span>服务/商城</span>
                        <span>SYSTEM</span>
                    </a>
                </li>
                <li><a href="">
                        <span>游戏安全</span>
                        <span>SECURITY</span>
                    </a></li>
                <li><a href="">
                        <span>玩家社区</span>
                        <span>COMMUNITY</span>
                    </a></li>
                <div class="header-bottom" v-show="flag">
                    <ul v-for="(item, index) in navData" :key="index">
                        <li v-for="(it, index) in item" :key="index">
                            <a href="#">
                                <span>
                                    <a href="">{{ it.name }}</a>
                                </span>
                            </a>
                        </li>
                    </ul>
                </div>
            </ul>
        </div>

    </div>
</template>
<script>
import axios from 'axios'
import { navTopList } from '@/data/nav-top.js'
const Banner = {
    data() {
        return {
            navData: [],
            flag: false
        }
    },
    computed: {
        dynamicClass() {
            // return ['header-banner-total', this.flag ? 'header-banner-total__active' : '']
            return {
                'header-back': true,
                'header-back__active': this.flag
            }
        }
    },
    mounted() {
        this.getNavDataList()
    },
    methods: {
        async getNavDataList() {
            try {
                let res = navTopList
                // let res = await axios.get("/nav-top.json")
                // console.log(res.data.data);
                this.navData = res // axios -> res.data.data
            } catch (error) {
                console.log(error);
            }
        },
        increase() {
            this.flag = true
        },
        remove() {
            this.flag = false
        }
    },

}
export default Banner
</script>

<style lang="less" scoped>
.header-banner-total {
    position: relative;



    .header-back {
        height: 382px;
        width: 100%;
        position: absolute;
        z-index: 5;
        background: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));

        &__active {
            background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0));
        }
    }

    .header-banner {
        height: 382px;
        background-image: url(../../assets/header/superBigIMG.jpeg);
        background-size: cover;

        .banner-ul {
            position: relative;
            padding: 30px 0;
            width: 1000px;
            margin: 0 auto;
            display: flex;
            z-index: 15;


            .header-bottom {
                a {
                    color: #fff;
                }

                position: absolute;
                width: 1000px;
                margin: 0 auto;
                display: flex;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                top: 90px;
                z-index: 50;
                height: 200px;

                ul {
                    padding: 0px;
                    text-align: center;
                    flex: 1;

                    li {
                        margin: 10px 0;

                        span {
                            font-size: 14px;
                        }

                        a:hover {
                            color: red;
                        }
                    }
                }
            }

            li {
                text-align: center;
                flex: 1;

                a:hover span {
                    color: red;
                }

                span {

                    font-size: 17px;
                    color: #fff;
                    display: block;

                    &:nth-child(2) {
                        font-size: 12px;
                        color: #777;
                    }
                }
            }
        }
    }

}
</style>